export const parseRequestURL = () => {
    const url = location.hash.slice(2),
        request = {};
        
    request.resource = url.split('/');

    return request;
};

const postData = async (url, data) => {
    let res = await fetch(url, {
        method: "POST",
        headers: {
            'Content-Type': 'application/json'
        },
        body: data
    });

    return await res.json();
};

